<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">采购订单</div>

      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row">
          <div class="columns">
            <span class="label">采购单ID:</span>
            <span class="value">{{ form.id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购单号:</span>
            <a-input v-model="form.purchase_id" style="width: 200px;" />
          </div>
        </div>
        
        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">国内运单号:</span>
            <div>
              <div v-for="posting_number,index in posting_numbers" :key="link+index">
                <span>运单号{{ index+1 }}号：</span>
                <a-input v-model="posting_numbers[index].value" style="width: 200px; margin-right: 10px;"/>
                <a-select v-model="posting_numbers[index].signed" placeholder="请选择" style="width: 100px; margin-right: 10px;"> 
                  <a-select-option v-for="option in signed" :value="option" :key="option">{{ option }}</a-select-option> 
                </a-select>
              </div>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购数量:</span>
            <a-input v-model="form.quantity" style="width: 100px;"/>
          </div>

          <div class="columns">
            <span class="label">已签收数量:</span>
            <a-input v-model="form.quantity_stock_in" style="width: 100px;"/>
          </div>

          <div class="columns">
            <span class="label">采购价格:</span>
            <a-input v-model="form.price" style="width: 100px;"/>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">物流状态:</span>
            <a-input v-model="form.logistics_status" style="width: 535px;"/>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购平台:</span>
            <a-select v-model="form.purchase_platform" placeholder="请选择" style="width: 200px;"> 
              <a-select-option v-for="option in options" :value="option" :key="option">{{ option }}</a-select-option> 
            </a-select> 
          </div>

          <div class="columns">
            <span class="label" style="width: 115px;">采购状态:</span>
            <a-select v-model="form.status" placeholder="请选择" style="width: 200px;"> 
              <a-select-option v-for="option in options_status" :value="option" :key="option">{{ option }}</a-select-option> 
            </a-select> 
          </div>
        </div>
        
        <div>
          <h3 style="margin-top: 10px;">关系统内商品信息</h3>

          <div class="row">
            <div class="columns">
              <img :src="form.system_product.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">商品ID:</span>
              <span class="value">{{ form.system_product.id }}</span>
            </div>
          </div>
          
          <div class="row">
            <div class="columns">
              <span class="label">系统sku:</span>
              <span class="value">{{ form.system_product.system_sku }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">参考重量:</span>
              <span class="value">{{ form.system_product.reference_weight }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">参考价格:</span>
              <span class="value">{{ form.system_product.reference_cost }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">采购备注:</span>
              <span class="value">{{ form.system_product.purchase_mark }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">打包备注:</span>
              <span class="value">{{ form.system_product.pack_mark }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">采购链接:</span>
              <div>
                <div v-for=" link,index in JSON.parse(form.system_product.purchase_link)" :key="index+100">
                  <span> {{ index+1 }}. </span>
                  <a :href="link" target="_blank"> {{ link }} </a>
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">采购平台:</span>
              <span class="value">{{ form.system_product.purchase_platform }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span class="label">供应商名称:</span>
              <span class="value">{{ form.system_product.supplier_name }}</span>
            </div>
          </div>

          <div class="row">
            <div class="columns">
              <span>库存量:</span>
              <span class="value">{{ form.system_product.stock_quantity }}</span>
            </div>
            <div class="columns">
              <span>缺货量:</span>
              <span class="value">{{ form.system_product.omitted_quantity }}</span>
            </div>
            <div class="columns">
              <span>在途量:</span>
              <span class="value">{{ form.system_product.in_transit_quantity }}</span>
            </div>
          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      options:["pdd","1688","线下"],
      options_status:["待采购","运输中","已完成","有异常","已作废"],
      posting_numbers:this.form.posting_numbers?JSON.parse(this.form.posting_numbers):[],
      signed:["未入库","已入库"]
    };
  },
  computed: {
  },
  watch:{
    posting_numbers:{
      handler(newValue){
        this.form.posting_numbers = JSON.stringify(newValue)
      },
      deep:true
    },
    visible(newValue) {
      this.posting_numbers = this.form.posting_numbers?JSON.parse(this.form.posting_numbers):[]
    }
  },
  methods: {
    confirm() {
      this.$emit('cancel', false);
    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.row .columns .label{
  width: 100px;
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>